      formcode: "FQ12-06-00",
      nationalno: "",
      form: {
        cfname: "",
        clname: "",
        father: "",
        edudepname: "",
      },
    };
  },
  methods: {
    async submitForm() {
      await axios
        .post("http://localhost:3000/forms/", {
          formcode: this.formcode,
          nationalno: this.nationalno,
          content: this.form,
        })
        .then((res) => {
          if (res.status == 200) {
            alert("  ");
            window.location.reload();
          } else {
            alert("   ");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
