<script>
import axios from "axios";

export default {
  name: "FQ12-07-00",
  data: () => {
    return {
      form: {},
    };
  },
  methods: {
    async deleteit(id) {
      await axios
        .delete("http://localhost:3000/forms/" + id)
        .then((res) => {
          if (res.status == 200) {
            alert("  ");
            window.location.reload();
          } else {
            alert("  ");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  async mounted() {
    await axios
      .get("http://localhost:3000/forms/single/" + this.$route.params.id)
      .then((res) => {
        if (res.status == 200) {
          this.form = res.data;
        } else {
          alert("   ");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

